  import { useState, useEffect, useCallback, useRef, useMemo } from "react";
  import { useNavigate, useLocation } from 'react-router-dom';
  import { Helmet } from 'react-helmet-async';
  import { v4 as uuidv4 } from "uuid";
  import JoditEditor from "jodit-react";
  import PageWrapper from "../../features/PageWrapper";

  import HTMLReactParser from "html-react-parser";
  import { ToastContainer, toast } from "react-toastify";
  import "react-toastify/dist/ReactToastify.css";
  import Flatpickr from "react-flatpickr";
  import "flatpickr/dist/flatpickr.min.css";
  import { Russian } from "flatpickr/dist/l10n/ru.js";
  import { translateRole } from '../../utils/helpers';

  import Lightbox from "yet-another-react-lightbox";
  import Fullscreen from "yet-another-react-lightbox/plugins/fullscreen";
  import Thumbnails from "yet-another-react-lightbox/plugins/thumbnails";
  import Zoom from "yet-another-react-lightbox/plugins/zoom";
  import "yet-another-react-lightbox/plugins/thumbnails.css";
  import "yet-another-react-lightbox/styles.css";

  import { api } from '../../services/apiClient';
  import { getAuthToken, isTokenValid } from '../../services/authHelpers';

  function NewsCreator() {

    const navigate = useNavigate();
    const location = useLocation();

    const [currentUser, setCurrentUser] = useState(null);

    const [nickname, setNickname] = useState("");
    
    const [event_start, setDate] = useState("");

    const editorRef = useRef(null);

    const titleRef = useRef("");

    const [description, setDescription] = useState("");

    const [newsImages, setNewsImages] = useState([]);
    const [previewMode, setPreviewMode] = useState(false);

    const [dragActive, setDragActive] = useState(false);

    const [categories, setCategories] = useState([]);
    const [selectedCategory, setSelectedCategory] = useState('');

    //    
    useEffect(() => {
      const loadCategoriess = async () => {
        try {
          const cats = await api.get('/categories');
          setCategories(cats);
        } catch (error) {
          toast.error('    ');
        }
      };
      loadCategoriess();
    }, []);


    // Lightbox State
    const [lightboxOpen, setLightboxOpen] = useState(false);
    const [lightboxIndex, setLightboxIndex] = useState(0);
    const [lightboxSlides, setLightboxSlides] = useState([]);


    //     
    const [isEditMode, setIsEditMode] = useState(false);
    const [editNewsId, setEditNewsId] = useState(null);

    const [users, setUsers] = useState([]);
    const [selectedAuthor, setSelectedAuthor] = useState('');


    //  JoditEditor
    const configJoditEditor = useMemo(() => ({
      toolbarAdaptive: false,
      showCharsCounter: false,
      showWordsCounter: false,
      showXPathInStatusbar: false,
      allowResizeX: false,
      autofocus: false,
      saveModeInStorage: true,
      askBeforePasteHTML: false,
      askBeforePasteFromWord: true,
      toolbarButtonSize: "large",
      placeholder: '  ',
      removeEmptyBlocks: true, //   <p></p>  
      cleanHTML: {
        removeEmptyElements: true,
        fillEmptyParagraph: false,
        removeOnPaste: true,
        removeAttributes: ['style', 'class'],
        denyTags: ['font', 'style'],
      },
      defaultActionOnPaste: 'insert_clear_html',
      enter: 'div', //     <p>  &nbsp;,    'p'  'div'
      minHeight: 200,
      disablePlugins:
        "video,about,add-new-line,class-span,source,resizer," +
        "speech-recognize,spellcheck,stat,drag-and-drop," +
        "drag-and-drop-element,clipboard,copyformat," +
        "delete-command,file,format-block,hotkeys," +
        "iframe,image,image-processor,image-properties,indent," +
        "inline-popup,media,paste-from-word," +
        "paste-storage,powered-by-jodit,print,search,sticky," +
        "symbols,table,table-keyboard-navigation,wrap-nodes",
      buttons:
        "undo redo | fontsize | bold italic underline brush link | align ol ul | eraser | fullsize |",
      events: {
        beforePaste(event, editor) {
          const clipboardData = event.clipboardData || window.clipboardData;
          if (clipboardData) {
            const items = clipboardData.items;
            for (const item of items) {
              if (item.type.startsWith("image/")) {
                event.preventDefault();
                toast.warn("  !");
                return false;
              }
            }
          }
        },
        beforePasteInsert(html) {
          const cleaned = html
            .replace(/<img[^>]*>/gi, '')
            .replace(/<svg[\s\S]*?<\/svg>/gi, '')
            .replace(/<style[\s\S]*?<\/style>/gi, '')
            .replace(/<link[^>]*>/gi, '')
            .replace(/style="[^"]*"/gi, '')
            .replace(/class="[^"]*"/gi, '')
            .replace(/&nbsp;/gi, ' ');

          //  <div>  <p> ()
          const normalized = cleaned
            .replace(/<div>/gi, '<p>')
            .replace(/<\/div>/gi, '</p>')
            .replace(/<br\s*\/?>/gi, '</p><p>');

          toast.warn("HTML      !");
          return normalized;
        },

        onPaste(event) {
          const html = event.clipboardData?.getData("text/html") || "";
          if (html) {
            event.preventDefault();

            const cleaned = html
              .replace(/<img[^>]*>/gi, '')
              .replace(/<svg[\s\S]*?<\/svg>/gi, '')
              .replace(/<style[\s\S]*?<\/style>/gi, '')
              .replace(/<link[^>]*>/gi, '')
              .replace(/style="[^"]*"/gi, '')
              .replace(/class="[^"]*"/gi, '')
              .replace(/&nbsp;/gi, ' ')
              .replace(/<div>/gi, '<p>')
              .replace(/<\/div>/gi, '</p>')
              .replace(/<br\s*\/?>/gi, '</p><p>');

            const plainText = event.clipboardData?.getData("text/plain") || "";
            const content = cleaned.trim() === '' && plainText.trim() !== '' ? plainText : cleaned;

            const editor = editorRef.current?.editor;
            if (editor) {
              editor.selection.insertHTML(content);
              toast.warn("  !");
            }
          }
        },    
        drop(event, editor) {
          const dt = event.dataTransfer;
          if (dt && dt.files.length > 0) {
            for (const file of dt.files) {
              if (file.type.startsWith("image/")) {
                event.preventDefault();
                toast.warn("  !");
                return false;
              }
            }
          }
        },
        beforeDrop(event) {
          const items = event?.dataTransfer?.items || [];
          for (const item of items) {
            if (item.kind === "file" && item.type.startsWith("image/")) {
              event.preventDefault();
              toast.warn("  !");
              return false;
            }
          }
        }
      }
    }), []);

    const configFlatpickr = useMemo(() => ({
      enableTime: true,
      altInput: true,
      altFormat: "F j, Y, H:i",
      dateFormat: "Y-m-d\\TH:i:ss",
      locale: Russian,
    }), []);
    
    //  Toast
    const configToast = useMemo(() => ({
      position: "top-right",
      autoClose: 4000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
    }), []);

    // Debounce  
    function useDebounce(value, delay) {
      const [debouncedValue, setDebouncedValue] = useState(value);

      useEffect(() => {
        const handler = setTimeout(() => {
          setDebouncedValue(value);
        }, delay);

        return () => {
          clearTimeout(handler);
        };
      }, [value, delay]);

      return debouncedValue;
    }

    const [title, setTitle] = useState("");
    const debouncedTitle = useDebounce(title, 300);

    const handleTitleChange = useCallback((e) => {
      titleRef.current = e.target.value;
      setTitle(e.target.value);
    }, []);

    const handleFiles = (files) => {
      const fileInput = document.getElementById("news-image");
      if (!fileInput) return; //    
      
      const validImages = [];
      const invalidFiles = [];
    
      files.forEach(file => {
        if (file.type.startsWith('image/')) {
          const uniqueKey = uuidv4();
          file.uniqueKey = uniqueKey;
          validImages.push({
            id: uniqueKey,
            file: file,
            preview: URL.createObjectURL(file)
          });
        } else {
          invalidFiles.push(file.name);
        }
      });
    
      if (invalidFiles.length > 0) {
        toast.warn(`   : ${invalidFiles.join(', ')}.`, {
          autoClose: 5000,
          closeButton: true
        });
        
        //       
        document.getElementById('news-image').value = '';
      }
    
      if (validImages.length > 0) {
        setNewsImages(prev => [...prev, ...validImages]);
      }
    };
    
    const handleDragEnter = (e) => {
      e.preventDefault();
      e.stopPropagation();
      setDragActive(true);
    };
    
    const handleDragLeave = (e) => {
      e.preventDefault();
      e.stopPropagation();
      setDragActive(false);
    };
    
    const handleDragOver = (e) => {
      e.preventDefault();
      e.stopPropagation();
      setDragActive(true);
    };

    const handleDrop = (e) => {
      e.preventDefault();
      e.stopPropagation();
      setDragActive(false);
    
      const files = Array.from(e.dataTransfer.files);
      const hasInvalidFiles = files.some(file => !file.type.startsWith('image/'));
    
      if (hasInvalidFiles) {
        toast.warn('    (JPG, PNG, GIF)!', {
          autoClose: 5000,
        });
        return; //  ,    
      }
    
      handleFiles(files); //    
    };

    const handleDeleteThumbnail = (thumbnailId) => {
      setNewsImages(prev => prev.filter(image => image.id !== thumbnailId));

      //        
      const fileInput = document.getElementById("news-image");
      if (fileInput && fileInput.files) { //  
        const dataTransfer = new DataTransfer();
        
        Array.from(fileInput.files).forEach(file => {
          if (file.uniqueKey !== thumbnailId) {
            dataTransfer.items.add(file);
          }
        });

        fileInput.files = dataTransfer.files;
      }
    };

    const handleDeleteAllThumbnails = () => {
      setNewsImages([]);
      const fileInput = document.getElementById("news-image");
      if (fileInput) {
        fileInput.value = ""; //  input
      }
    };

      
    //    
    const openLightbox = (index) => {
      setLightboxSlides(newsImages.map(img => ({
        src: img.file ? URL.createObjectURL(img.file) : img.preview, //     
        alt: ` ${index + 1}`
      })));
      setLightboxIndex(index);
      setLightboxOpen(true);
    };

    const handlePreview = () => {
      setPreviewMode(!previewMode);
    };

    const handleRestartPreview = () => {
      setPreviewMode(false);
    };


    //     
    const loadUsers = useCallback(async () => {
      try {
        const usersList = await api.get('/admin/users');
        setUsers(usersList);
        
        //       
        if (isEditMode && nickname) {
          setSelectedAuthor(nickname);
        }
      } catch (error) {
        console.error('Error loading users:', error);
        toast.error('    ', configToast);
      }
    }, [configToast, isEditMode, nickname]);

    //     
    useEffect(() => {
      if (currentUser?.role === 'Administrator' || currentUser?.role === 'Moderator') {
        loadUsers();
      }
    }, [currentUser?.role, loadUsers]);

    //      nickname
    useEffect(() => {
      if (nickname) {
        setSelectedAuthor(nickname);
      }
    }, [nickname]);


    useEffect(() => {
      const checkAuth = async () => {
        try {
          const token = getAuthToken();

          if (!token || !isTokenValid(token)) {
            localStorage.removeItem('user');
            sessionStorage.removeItem('user');
            api.setAuthToken(null);

            navigate('/login', { replace: true });
            setTimeout(() => {
              toast.error(" , ,  .", {
                autoClose: 4000,
              });
            }, 100);
            return;
          }

          const userData = JSON.parse(
            localStorage.getItem("user") || sessionStorage.getItem("user") || "null"
          );

          if (!userData) {
            throw new Error("  ");
          }

          setCurrentUser(userData);
          setNickname(userData.nickname || '');
          api.setAuthToken(userData.token);

        } catch (error) {
          console.error('Auth check failed:', error);
          localStorage.removeItem('user');
          sessionStorage.removeItem('user');
          api.setAuthToken(null);

          navigate('/login', { replace: true });
          setTimeout(() => {
            toast.error(error.message || ' ', {
              autoClose: 4000,
            });
          }, 100);
        }
      };

      checkAuth();
    }, [navigate]);


    const loadNewsData = useCallback(async (newsId) => {
      try {
        const formData = await api.get(`/news/${newsId}`);
        
        setNickname(formData.publisher_nick || "");
        setSelectedCategory(formData.categoryID || "");
        setTitle(formData.title || "");
        setDescription(formData.description || "");
        
        if (formData.event_start) {
          setDate(new Date(formData.event_start));
        }
        
        if (formData.files && formData.files.length > 0) {
          const images = formData.files.map(file => ({
            id: uuidv4(),
            fileName: file.fileName,
            preview: `http://127.0.0.1:5000/uploads/${file.fileName}`
          }));
          setNewsImages(images);
        }
      } catch (error) {
        console.error("Error loading news data:", error);
        toast.error(error.message || "    ", configToast);
      }
    }, [configToast]);


    useEffect(() => {
      const params = new URLSearchParams(window.location.search);
      const newsId = params.get('edit');
      
      if (newsId) {
        setIsEditMode(true);
        setEditNewsId(newsId);
        loadNewsData(newsId);
      }
    }, [loadNewsData]); //    


    const handleSubmit = async (e) => {
      e.preventDefault();

      //  
      if (!currentUser || !currentUser.nickname) {
        toast.error(" !", configToast);
        return;
      }

      //  
      if (!event_start || event_start.length === 0) {
        toast.error(",   ", configToast);
        return;
      }

      if (!title.trim()) {
        toast.error(",   ", configToast);
        return;
      }

      if (!description.trim()) {
        toast.error(",   ", configToast);
        return;
      }

      //     
      let authorNickname = currentUser.nickname;
      let authorLogin = currentUser.login;

      //  /   
      if ((currentUser?.role === 'Administrator' || currentUser?.role === 'Moderator') && isEditMode) {
        if (!selectedAuthor) {
          toast.error('   !', configToast);
          return;
        }

        //   
        const selectedUser = users.find(user => user.nick === selectedAuthor);
        if (!selectedUser) {
          toast.error('   !', configToast);
          return;
        }

        authorNickname = selectedUser.nick;
        authorLogin = selectedUser.login;
      }

      //  FormData
      const formData = new FormData();
      formData.append("login", authorLogin); //    
      formData.append("categoryID", selectedCategory);
      formData.append("nickname", authorNickname); //    
      formData.append("title", title);
      formData.append("description", description);
      formData.append("event_start", new Date(event_start).toISOString());
      
      //   "Pending"    
      if (!isEditMode) {
        formData.append("status", "Pending");
      }

      //    
      const existingFiles = newsImages
        .filter(img => img.fileName)
        .map(img => img.fileName);

      const newFiles = newsImages
        .filter(img => img.file)
        .map(img => img.file);

      //      
      if (isEditMode) {
        if (existingFiles.length === 0 && newsImages.length === 0) {
          formData.append("delete_all_files", "true");
        } else {
          existingFiles.forEach(fileName => {
            formData.append("existing_files", fileName);
          });
        }
      }

      //   
      newFiles.forEach(file => {
        formData.append("files", file);
      });

      try {
        await (isEditMode
          ? api.put(`/news/${editNewsId}`, formData, true)
          : api.post("/news", formData, true)
        );

        if (isEditMode) {
          navigate('/news-list')
          setTimeout(() => {
            toast.success("  !", configToast);
          }, 200);
        } else {
          toast.success("   !", configToast);
        }
          
      } catch (error) {
        console.error("Request error:", error);
        
        if (error.message.includes("401")) {
          navigate('/login', { 
            state: { from: location },
            replace: true 
          });
          return;
        }
      
        toast.error(
          error.message || "   ",
          configToast
        );
      }
    };

    //    (   )
    const handleDateChange = (selectedDate) => {
      setDate(selectedDate);
    };

    const MemoizedFlatpickr = useMemo(() => {
      return (
        <Flatpickr
          name="event_start"
          placeholder="  "
          options={configFlatpickr} // onChange   configFlatpickr
          value={event_start}
          onChange={handleDateChange}
        />
      );
    }, [configFlatpickr, event_start]);

    const MemoizedToastContainer = useMemo(() => <ToastContainer options={configToast}/>, [configToast]);

    
    return (
      <PageWrapper>
      <Helmet>
        <title> </title>
      </Helmet>
        <div id="news-form" className="container">
          <h1> </h1>
          {currentUser && (
            <div className="user-info">
              <p>
                 : {currentUser.nickname} ({currentUser.login})
              </p>
              <p>: {translateRole(currentUser.role)}</p>
            </div>
          )}
          <div className="content">
            <form className="form" name="newsForm" onSubmit={handleSubmit}>
              {/*    ,  Publisher */}
              {(currentUser?.role === 'Administrator' || currentUser?.role === 'Moderator') && isEditMode && (
                <div className="form-group">
                  <label htmlFor="news-author"> :</label>
                  <select
                    id="news-author"
                    name="author"
                    className="inpt"
                    value={selectedAuthor}
                    onChange={(e) => setSelectedAuthor(e.target.value)}
                  >
                    {users.map(user => (
                      <option key={user.userID} value={user.nick}>
                        {user.nick} ({user.login})
                      </option>
                    ))}
                  </select>
                </div>
              )}

              <div className="form-group">
                <select
                  value={selectedCategory}
                  onChange={(e) => setSelectedCategory(e.target.value)}
                >
                  <option value=""> </option>
                  {categories.map(cat => (
                    <option key={cat.categoryID} value={cat.categoryID}>
                      {cat.name}
                    </option>
                  ))}
                </select>
              </div>

              {MemoizedFlatpickr}

              <input
                type="text"
                id="news-title"
                name="title"
                className="inpt"
                placeholder="  "
                value={title}
                onChange={handleTitleChange}
              />

              <JoditEditor
                name="description"
                defaultValue={description || ""}
                config={configJoditEditor}
                tabIndex={1}
                ref={editorRef}
                onBlur={(newText) => setDescription(newText)}
                onChange={(newText) => setDescription(newText)}
              />

              <div 
                id="drop-zone" 
                className={`drop-zone ${dragActive ? 'drag-active' : ''}`}
                onDrop={handleDrop}
                onDragOver={handleDragOver}
                onDragEnter={handleDragEnter}
                onDragLeave={handleDragLeave}
              >
                <p>       (JPG, PNG, GIF)</p>
                <input
                  type="file"
                  id="news-image"
                  name="files[]"
                  accept="image/jpeg,image/png,image/gif"
                  multiple
                  onChange={(e) => {
                    const files = Array.from(e.target.files);
                    if (files.some(file => !file.type.startsWith('image/'))) {
                      e.target.value = '';
                    }
                    handleFiles(files);
                  }}
                />
              </div>

              {newsImages.length > 0 && (
                <div id="thumbnail-container" className="thumbnail-container">
                  {newsImages.map((image, index) => (
                    <div key={image.id} className="thumbnail">
                      <img 
                        src={image.preview} 
                        alt="Thumbnail" 
                        onClick={() => openLightbox(index)}
                      />
                      <span 
                        className="delete-button" 
                        onClick={() => handleDeleteThumbnail(image.id)}
                      >
                        
                      </span>
                    </div>
                  ))}
                </div>
              )}

              <Lightbox
                open={lightboxOpen}
                close={() => setLightboxOpen(false)}
                index={lightboxIndex}
                slides={lightboxSlides}
                plugins={[Fullscreen, Thumbnails, Zoom]}
                styles={{
                  container: { backgroundColor: "rgba(0, 0, 0, 0.8)" },
                  thumbnail: {
                    backgroundColor: "rgba(0, 0, 0, 0.5)",
                  },
                  thumbnailsContainer: {
                    backgroundColor: "rgba(0, 0, 0, 0.8)",
                  },
                  icon: {
                    color: "rgba(255, 255, 255, 0.7)",
                    filter: "drop-shadow(0 0 2px rgba(0, 0, 0, 0.5))",
                  },
                  iconDisabled: {
                    color: "rgba(255, 255, 255, 0.3)",
                  },
                  iconHover: {
                    color: "#fff",
                    backgroundColor: "rgba(0, 0, 0, 0.3)",
                  }
                }}
                thumbnails={{
                  vignette: false,
                }}
                zoom={{
                  maxZoomPixelRatio: 4, //   
                  zoomInMultiplier: 1.2,  //  
                  scrollToZoom: true    //   
                }}
              />

              {newsImages.length > 0 && (
                <button
                  className="custom_button action-remove"
                  onClick={handleDeleteAllThumbnails}
                  type="button" 
                >
                    
                </button>
              )}

              {previewMode ? (
                <div className="preview-container">
                  <button
                    className="custom_button"
                    onClick={handleRestartPreview}
                    type="button"
                  >
                      
                  </button>

                  {title && <h3 className="preview-title">{HTMLReactParser(debouncedTitle)}</h3>}
                  {description && <div>{HTMLReactParser(description)}</div>}
                </div>
              ) : (
                <button
                  className="custom_button"
                  onClick={handlePreview}
                  type="button"
                >
                   
                </button>
              )}
            
              <button type="submit" className="custom_button action-confirm">
                {isEditMode ? " " : " "}
              </button>
              
            </form>

          </div>
        </div>
        {MemoizedToastContainer}
      </PageWrapper>
    );
  }

  export default NewsCreator;