import React from 'react';
import PropTypes from 'prop-types';
import Flatpickr from 'react-flatpickr';
import { Russian } from 'flatpickr/dist/l10n/ru.js';
import 'flatpickr/dist/flatpickr.min.css';

const CategoriesFilters = ({
  dateRange,
  searchFilter,
  onSearchChange,
  onDateChange,
  onClear,
  onDeleteAll,
  deleteDisabled
}) => {
  const configFlatpickr = {
    mode: "range",
    altInput: true,
    altFormat: "F j, Y",
    dateFormat: "Y-m-d",
    locale: Russian,
    onChange: (selectedDates) => onDateChange(selectedDates)
  };

  return (
    <div className="filters-container" style={{ padding: '12px 0 0 0' }}>
      <div className="filter-group">
        <label htmlFor="search-categories">  </label>
        <input
          id="search-categories"
          type="text"
          placeholder=" ..."
          value={searchFilter}
          onChange={(e) => onSearchChange(e.target.value)}
        />
      </div>

      <div className="filter-group">
        <label htmlFor="date-filter">  </label>
        <Flatpickr
          options={configFlatpickr}
          value={dateRange}
          placeholder=" "
          className="date-input"
        />
      </div>

      <button
        onClick={onClear}
        className="custom_button_long"
        disabled={!searchFilter && !dateRange[0]}
      >
          
      </button>

      <button
        onClick={onDeleteAll}
        className="custom_button_long action-remove"
        id="delete-all"
        disabled={deleteDisabled}
      >
          
      </button>
    </div>
  );
};

CategoriesFilters.propTypes = {
  dateRange: PropTypes.array.isRequired,
  searchFilter: PropTypes.string,
  onSearchChange: PropTypes.func.isRequired,
  onDateChange: PropTypes.func.isRequired,
  onClear: PropTypes.func.isRequired,
  onDeleteAll: PropTypes.func.isRequired,
  deleteDisabled: PropTypes.bool.isRequired  
};

export default React.memo(CategoriesFilters);